.navbar {
    padding: 6px 35px;
    border-bottom: 1px solid rgb(199, 199, 199);
}

.navbar a {
    color: black !important;
}

.navbar .navbar-brand {
    font-family: 'Cinzel Decorative', cursive;
    font-size: 2rem;
    font-weight: bold;
    margin-right: 2rem;
}

.navbar .items {
    font-family: Helvetica, sans-serif;
    font-weight: lighter;
    font-size: 1rem;
    width: 600px;
    justify-content: space-between;
}

.navbar .items a:hover {
    color: rgb(17, 109, 255) !important;
}

.navbar .social-links {
    font-size: 2rem;
    flex-direction: row;
}

.navbar .social-links a {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0;
    padding-bottom: 0;
}

.navbar .social-links a:hover {
    color: rgb(17, 109, 255) !important;
}

.navbar .social-links a:first-of-type {
    padding-left: 0;
}

.navbar .social-links a:last-of-type {
    padding-right: 0;
}

.jumbotron {
    padding-left: 1rem;
    padding-right: 1rem;
    margin-bottom: 0;
    border-radius: 0;
    background-color: white;
}

