body {
    color: #343434;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    line-height: 1.5;
}

img {
    width: 300px;
}

a {
    color: #FFE66D;
}

a:hover {
    text-decoration: none;
}

.content-wrap {
    margin: 0 auto;
    padding: 40px 0;
    max-width: 800px;
    width: 85%;
}

h1, h2, h3, ul, p {
    margin: 0;
}

.divider>section {
    border-bottom: 1px dashed #343434;
    padding: 20px 0;
}

.divider>section:last-of-type {
    border-bottom: none;
}

/* Profile
    ------------------------------------*/

header {
    background: #2F3061;
    color: #F7FFF7;
}

/* Projects
    ------------------------------------*/

.projects {
    background: #F7FFF7;
}

.projects a {
    color: #2F3061;
}

.projects .btn {
    color: #F7FFF7;
    background: #2F3061;
    text-decoration: none;
    padding: 8px;
    border-radius: 4px;
    display: inline-block;
}

.projects .btn:hover {
    background: #2f3061cc;
}

/* Work Experience
    ------------------------------------*/

.work-experience {
    background: #A1C6D7;
}

.job-item {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 20px;
}

